import React from "react";

import classes from "./Footer.module.css";

const footer = (props) => {
  return (
    <section className={classes.Footer}>
      <h3>Palm Properties</h3>
      <ul>
        <li>Facebook</li>
        <li>Instagram</li>
        <li>Twitter</li>
      </ul>
      <p>Copyright by Lewis Yates</p>
    </section>
  );
};

export default footer;
