import * as actionTypes from "./actionTypes";
import axios from "axios";

export const setIsAuthorized = (value) => {
  return (dispatch) => {
    if (!value) {
      axios
        .get("/api/authenticate/logout")
        .then((response) => {
          dispatch({
            type: actionTypes.SET_IS_AUTH,
            value: value,
          });
        })
        .catch((err) => {});
    } else {
      dispatch({
        type: actionTypes.SET_IS_AUTH,
        value: value,
      });
    }
  };
};

