import React from "react";
import classes from "./ConfirmModal.module.css";

const ConfirmModal = (props) => {
  const days = Math.ceil(
    (props.selectedDates[1] - props.selectedDates[0]) / 24 / 60 / 60 / 1000
  );
  const totalPrice = props.bookingDetails.pricePerNight * days;

  const fromDate = `${
    props.monthsRef[props.selectedDates[0].getMonth()]
  } ${props.selectedDates[0].getDate()} ${props.selectedDates[0].getFullYear()}`;

  const toDate = `${
    props.monthsRef[props.selectedDates[1].getMonth()]
  } ${props.selectedDates[1].getDate()} ${props.selectedDates[1].getFullYear()}`;

  return (
    <div className={classes.confirmModal}>
      <h1>Booking Confirmation</h1>
      <p>
        <strong>Booking for: </strong>
        {props.bookingDetails.name.replaceAll("-", " ")}
      </p>
      <p>
        <strong>Selected Dates: </strong>
        {fromDate} to {toDate}
      </p>
      <p>
        <strong>Days booked: </strong>
        {days}
      </p>
      <p>
        <strong>Total Price: </strong>£{totalPrice}
      </p>

      <button
        className={classes.ConfirmBtn}
        onClick={() =>
          props.confirmBooking({
            datesBooked: [fromDate, toDate],
            totalPrice: totalPrice,
            days: days,
          })
        }
      >
        PLACE BOOKING
      </button>
      <button className={classes.CancelBtn} onClick={props.closeModal}>
        CANCEL
      </button>
    </div>
  );
};

export default ConfirmModal;
