import * as actionTypes from "../actions/actionTypes";

const initialState = {
  isAuth: false,
};

const authReducer = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.SET_IS_AUTH:
      return {
        isAuth: action.value,
      };
    case "UNAUTHORIZE":
      return;
    default:
      return state;
  }
};

export default authReducer;
