import React from "react";
import { Link } from "react-router-dom";
import classes from "./Listings.module.css";

const listings = (props) => {
  return (
    <section className={classes.Listings}>
      <h1>Our available properties</h1>
      <div className={classes.Image1}></div>
      <div className={classes.Image2}></div>
      <div className={classes.Image3}></div>
      <h2>Think you deserve a getaway? Browse our listings!</h2>
      <p className={classes.para1}>
        Explore our listings, whether you're just here to browse or considering
        making a booking we are sure our getaways will satisfy you!
      </p>
      <Link to="/getaways">View our getaways</Link>
    </section>
  );
};

export default listings;
