import React, { useState } from "react";
import classes from "./Navigation.module.css";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import * as actions from "../../../store/actions/authActions";

const Navigation = (props) => {
  const [showNav, setShowNav] = useState(false);

  const handleLogout = () => {
    props.setIsAuth(false);
  };

  const toggleNav = () => {
    setShowNav(!showNav);
  };

  return (
    <React.Fragment>
      <span onClick={toggleNav} className={classes.MobileNavBtn}></span>
      <section
        onClick={toggleNav}
        className={[classes.Navigation, showNav ? null : classes.hidden].join(
          " "
        )}
      >
        <span className={classes.Logo}>
          <img src="/logo.png" alt="Logo" />
        </span>
        <ul className={classes.NavigationList}>
          <Link to="/">Home</Link>
          <Link to="/about-us">About us</Link>
          <Link to="/getaways">Getaways</Link>
          {props.isAuth ? <Link to="/myaccount">My Account</Link> : null}
          <Link to={props.isAuth ? "/" : "/signup"} onClick={handleLogout}>
            {props.isAuth ? "Logout" : "Sign Up / Login"}
          </Link>
        </ul>
      </section>
    </React.Fragment>
  );
};

const mapStateToProps = (state) => {
  return {
    isAuth: state.isAuth,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    setIsAuth: (value) => dispatch(actions.setIsAuthorized(value)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Navigation);
