import React from "react";

import classes from "./AboutUs.module.css";

const aboutUs = (props) => {
  return (
    <section className={classes.AboutUs}>
      <h1>About us</h1>
      <div className={classes.part1}>
        <h2>What we do</h2>
        <p className={classes.para1}>
          We have a variety of luxury villas and houses available for you to
          rent! We aim to provide you with only the best, at the lowest possible
          prices. From browsing online to relaxing on the beach, we make it an
          intuitive and easy process.
        </p>
      </div>
      <div className={classes.part2}>
        <h2>1, 2, 3</h2>
        <p className={classes.para2}>
          Simply browse for a listing, make a booking, relax!
        </p>
      </div>
    </section>
  );
};

export default aboutUs;
