import React, { useEffect } from "react";
import classes from "./Getaways.module.css";
import { Route, Switch } from "react-router-dom";
import Listings from "./Listings/Listings";
import HouseView from "./HouseView/HouseView";

const Getaways = (props) => {
  useEffect(() => {
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
  }, []);

  return (
    <section className={classes.Getaways}>
      <Switch>
        <Route path="/getaways/:id" component={HouseView} />
        <Route path="/getaways" exact component={Listings} />
      </Switch>
    </section>
  );
};

export default Getaways;
