import React from "react";
import classes from "./PageButtons.module.css";

const PageButtons = (props) => {
  const disabledStyling = {
    border: "1px solid #111111",
    color: "#bdbdbd",
    cursor: "not-allowed",
  };

  return (
    <div className={classes.PaginationButtons}>
      <button
        className={classes.PrevBtn}
        onClick={() => props.pageClick("prev")}
        disabled={!props.dynPage.hasPrevPage}
        style={props.dynPage.hasPrevPage ? null : disabledStyling}
      >
        Prev
      </button>
      <button
        className={classes.NextBtn}
        onClick={() => props.pageClick("next")}
        disabled={!props.dynPage.hasNextPage}
        style={props.dynPage.hasNextPage ? null : disabledStyling}
      >
        Next
      </button>
    </div>
  );
};

export default PageButtons;
