import React, { useEffect, useCallback } from "react";
import FrontPage from "./components/FrontPage/FrontPage";
import Getaways from "./containers/Getaways/Getaways";
import Layout from "./components/Layout/Layout";
import Signup from "./containers/Signup/Signup";
import axios from "axios";
import { connect } from "react-redux";
import * as actions from "./store/actions/authActions";
import MyAccount from "./containers/MyAccount/MyAccount";

import { BrowserRouter, Route, Switch } from "react-router-dom";

const App = (props) => {
  const handleSuccessResponse = useCallback(() => {
    props.setIsAuth(true);
  }, [props]);

  const handleFailedResponse = useCallback(() => {
    props.setIsAuth(false);
  }, [props]);

  useEffect(() => {
    axios
      .get("/api/authenticate/isauth")
      .then((response) => {
        if (response.data.status === "success") {
          handleSuccessResponse();
        } else {
          handleFailedResponse();
        }
      })
      .catch((err) => {});
  }, [handleSuccessResponse, handleFailedResponse]);

  return (
    <BrowserRouter>
      <Layout>
        <Switch>
          <Route path="/getaways" component={Getaways} />
          <Route path="/signup" component={Signup} />
          <Route
            path="/about-us"
            component={() => <FrontPage about={true} />}
          />
          {props.isAuth ? (
            <Route path="/myaccount" component={MyAccount} />
          ) : null}
          <Route path="/" component={FrontPage} />
        </Switch>
      </Layout>
    </BrowserRouter>
  );
};

const mapStateToProps = (state) => {
  return {
    isAuth: state.isAuth,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    setIsAuth: (value) => dispatch(actions.setIsAuthorized(value)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(React.memo(App));
