import React, { useState, useEffect } from "react";
import Window from "./Window/Window";
import axios from "axios";
import ErrorModal from "../../components/Utility/errorModal/errorModal";
import { connect } from "react-redux";
import * as actions from "../../store/actions/authActions";

const Signup = (props) => {
  const [response, setResponse] = useState(null);

  useEffect(() => {
    return () => {
      setResponse(null);
    };
  }, []);

  const handleFailedResponse = (message) => {
    setResponse({ status: "fail", message });
    setTimeout(() => {
      setResponse(null);
    }, 2000);
  };

  const handleSuccessResponse = (message) => {
    setResponse({ status: "success", message });
    props.setIsAuthorized(true);
    setTimeout(() => {
      props.history.replace("/");
    }, 2000);
  };

  const authenticateHandler = async (data) => {
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
    const postData = {
      name: data.name,
      email: data.email,
      password: data.pass,
      passwordConfirm: data.passConfirm,
    };
    if (data.method === "signup") {
      axios
        .post("/api/authenticate/signup", postData)
        .then((response) => {
          if (response.data.status === "failed") {
            handleFailedResponse(response.data.message);
          } else {
            const msg =
              data.method === "signup"
                ? "Successfully signed up!"
                : "Successfully logged in!";
            handleSuccessResponse(msg);
          }
        })
        .catch((err) => {
          handleFailedResponse("Something went wrong!");
        });
    } else {
      axios
        .post("/api/authenticate/login", postData)
        .then((response) => {
          if (response.data.status === "failed") {
            handleFailedResponse(response.data.message);
          } else {
            const msg =
              data.method === "signup"
                ? "Successfully signed up!"
                : "Successfully logged in!";
            handleSuccessResponse(msg);
          }
        })
        .catch((err) => {
          handleFailedResponse("Something went wrong!");
        });
    }
  };

  let modal = null;

  if (response) {
    if (response.status === "fail") {
      modal = <ErrorModal restype="fail" message={response.message} />;
    } else {
      modal = <ErrorModal restype="success" message={response.message} />;
    }
  }

  return (
    <React.Fragment>
      {modal}
      <Window clickHandler={authenticateHandler} />
    </React.Fragment>
  );
};

const mapDispatchToProps = (dispatch) => {
  return {
    setIsAuthorized: (value) => dispatch(actions.setIsAuthorized(value)),
  };
};

export default connect(null, mapDispatchToProps)(Signup);

