import React, { useState, useEffect } from "react";
import classes from "./MyAccount.module.css";
import bookingclasses from "./Bookings.module.css";
import Spinner from "../../components/UI/Spinner/Spinner";
import axios from "axios";
import ErrorModal from "../../components/Utility/errorModal/errorModal";

const MyAccount = (props) => {
  const [view, setView] = useState("bookings");

  const [userInfo, setUserInfo] = useState(null);

  const [bookings, setBookings] = useState(null);

  const [error, setError] = useState(null);

  const [success, setSuccess] = useState(null);

  const handleSuccessResponse = (data) => {
    setUserInfo({ name: data.data.name, email: data.data.email });
  };

  const handleFailedResponse = (data) => {
    if (userInfo) setUserInfo(null);
    setError(data.message);
    setTimeout(() => {
      setError(null);
      props.history.replace("/");
    }, 2000);
  };

  useEffect(() => {
    // REQUEST FOR USERS INFORMATION
    axios.get("/api/users/me").then((response) => {
      if (response.data.status === "success") {
        handleSuccessResponse(response.data);
      } else {
        handleFailedResponse(response.data);
      }
    });
    // REQUEST FOR USER BOOKINGS
    axios.get("/api/bookings/mybookings").then((response) => {
      if (response.data.status === "success") {
        setBookings(response.data.data);
      } else {
        handleFailedResponse({ data: "Something went wrong!" });
      }
    });
  }, []);

  const switchViewHandler = (type) => {
    if (view === type) return;
    setView(type);
  };

  const scrollToTop = () => {
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
  };

  const clearInputFields = () => {
    const inputFields = document.querySelectorAll("input");
    inputFields.forEach((inp) => {
      inp.value = "";
    });
  };

  const submitUpdatedDetails = async (event) => {
    event.preventDefault();
    const updatedName = document.getElementById("updateDetailsName").value;
    const updatedEmail = document.getElementById("updateDetailsEmail").value;
    axios
      .post("/api/users/updateme", { name: updatedName, email: updatedEmail })
      .then((response) => {
        if (response.data.status === "success") {
          setSuccess("Details updated!");
          clearInputFields();
          setUserInfo({ name: updatedName, email: updatedEmail });
          scrollToTop();
          setTimeout(() => {
            setSuccess(null);
          }, 2000);
        } else {
          handleFailedResponse({ message: "Something went wrong!" });
        }
      });
  };

  const submitUpdatedPassword = async (event) => {
    event.preventDefault();
    const currentPassword = document.getElementById("updatePassCurrent").value;
    const newPassword = document.getElementById("updatePassNew").value;
    const newPasswordConfirm = document.getElementById("updatePassNewConfirm")
      .value;
    axios
      .post("/api/users/updatemypass", {
        currentPassword,
        newPassword,
        newPasswordConfirm,
      })
      .then((response) => {
        if (response.data.status === "success") {
          setSuccess("Password updated!");
          clearInputFields();
          scrollToTop();
          setTimeout(() => {
            setSuccess(null);
          }, 2000);
        } else {
          // failed update password
          setError(response.data.message);
          scrollToTop();
          clearInputFields();
          setTimeout(() => {
            setError(null);
          }, 2000);
        }
      });
  };

  let content = <Spinner />;
  if (view === "account") {
    content = (
      <React.Fragment>
        <form className={classes.detailsForm} onSubmit={submitUpdatedDetails}>
          <h3>Update Details</h3>
          <label>Update Name</label>
          <input
            type="text"
            placeholder={userInfo ? userInfo.name : "New name"}
            required
            onChange={() => {}}
            id="updateDetailsName"
          />
          <label>Update Email</label>
          <input
            type="email"
            placeholder={userInfo ? userInfo.email : "New email"}
            required
            onChange={() => {}}
            id="updateDetailsEmail"
          />
          <button>Submit</button>
        </form>
        <form
          className={classes.updatePassForm}
          onSubmit={submitUpdatedPassword}
        >
          <h3>Update Password</h3>
          <label>Current Password</label>
          <input
            type="password"
            placeholder="Current password"
            required
            minLength="8"
            id="updatePassCurrent"
          />
          <label>New Password</label>
          <input
            type="password"
            placeholder="New password"
            required
            minLength="8"
            id="updatePassNew"
          />
          <label>Confirm Password</label>
          <input
            type="password"
            placeholder="Confirm password"
            required
            minLength="8"
            id="updatePassNewConfirm"
          />
          <button>Submit</button>
        </form>
        <h4>
          For security reasons regarding bookings, if you would like to delete
          your account please contact us at support@palmtree-getaways.com
        </h4>
      </React.Fragment>
    );
  } else if (bookings) {
    if (bookings.length === 0) {
      content = (
        <p className={bookingclasses.NoBookings}>You have no bookings yet!</p>
      );
    } else {
      content = (
        <React.Fragment>
          <h6 className={bookingclasses.BookingTitle}>My bookings</h6>
          {bookings.map((booking, i) => {
            return (
              <div className={bookingclasses.Booking} key={i + Math.random()}>
                <h5>{booking.listingId.name.replaceAll("-", " ")}</h5>
                <p>
                  <strong>Booked Dates: </strong>
                  {booking.datesBooked[0]} to {booking.datesBooked[1]}
                </p>
                <p>
                  <strong>Total Price: </strong>£{booking.totalPrice}
                </p>
                <img
                  src={`/listings/${booking.listingId.coverImage}`}
                  alt="bookingImage"
                />
              </div>
            );
          })}
        </React.Fragment>
      );
    }
  }

  return (
    <section className={classes.MyAccount}>
      {error ? <ErrorModal restype="fail" message={error} /> : null}
      {success ? <ErrorModal restype="success" message={success} /> : null}
      <h1>My Account</h1>
      <div className={classes.accountWindow}>
        <div className={classes.Sidebar}>
          <h2
            className={view === "bookings" ? classes.Active : null}
            onClick={() => switchViewHandler("bookings")}
          >
            My Bookings
          </h2>
          <h2
            className={view === "account" ? classes.Active : null}
            onClick={() => switchViewHandler("account")}
          >
            Update Account
          </h2>
        </div>
        {content}
      </div>
    </section>
  );
};

export default MyAccount;
