import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import classes from "./Results.module.css";
import Spinner from "../../../components/UI/Spinner/Spinner";
import axios from "axios";
import { AiFillStar, AiOutlineStar } from "react-icons/ai";
import { ImLocation } from "react-icons/im";
import { BsPersonFill } from "react-icons/bs";
import Transition from "react-transition-group/Transition";
import PageButtons from "./PageButtons/PageButtons";

const ratings = [1, 2, 3, 4, 5];

const Results = (props) => {
  const [results, setResults] = useState(null);

  const [animation, setAnimation] = useState(false);

  const [pagination, setPagination] = useState({
    page: 1,
    hasPrevPage: false,
    hasNextPage: true,
  });

  useEffect(() => {
    axios.post("/api/getaways/all", { requestedPage: 1 }).then((response) => {
      setResults(response.data.data.docs);
      setAnimation(true);
      setPagination({
        page: response.data.data.page,
        hasPrevPage: response.data.data.hasPrevPage,
        hasNextPage: response.data.data.hasNextPage,
      });
    });

    return () => {
      // clean up...
    };
  }, []);

  const scrollToTop = () => {
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
  };

  const handlePage = (direction) => {
    scrollToTop();
    if (direction === "prev") {
      setResults(null);
      setAnimation(false);
      axios
        .post("/api/getaways/all", { requestedPage: pagination.page - 1 })
        .then((response) => {
          setResults(response.data.data.docs);
          setAnimation(true);
          setPagination({
            page: response.data.data.page,
            hasPrevPage: response.data.data.hasPrevPage,
            hasNextPage: response.data.data.hasNextPage,
          });
        });
    } else {
      // next page
      setResults(null);
      setAnimation(false);
      axios
        .post("/api/getaways/all", { requestedPage: pagination.page + 1 })
        .then((response) => {
          setResults((prevState) => response.data.data.docs);
          setAnimation(true);
          setPagination({
            page: response.data.data.page,
            hasPrevPage: response.data.data.hasPrevPage,
            hasNextPage: response.data.data.hasNextPage,
          });
        });
    }
  };

  let listings;
  if (!results) {
    listings = <Spinner />;
  } else {
    listings = results.map((result, i) => {
      return (
        <div className={classes.result} key={result.name + i}>
          <div className={classes.Image}>
            <img src={`/listings/${result.coverImage}`} alt="coverImage" />
          </div>
          <h1>{result.name.replaceAll("-", " ")}</h1>
          <span>
            {ratings.map((r, i) =>
              r <= +result.ratingsAverage ? (
                <AiFillStar key={r + i} />
              ) : (
                <AiOutlineStar key={r + i} />
              )
            )}
          </span>
          <div className={classes.LocationPrice}>
            <p>
              <ImLocation /> {result.location}
            </p>
          </div>
          <div className={classes.FitsBeach}>
            <p>
              <BsPersonFill /> {result.detail.bedrooms} People
            </p>
          </div>
          <p className={classes.Price}>
            Price Per Night: £{result.pricePerNight}
          </p>
          <Link to={`/getaways/${result.slug}`}>View</Link>
        </div>
      );
    });
  }

  return (
    <React.Fragment>
      {results ? (
        <Transition in={animation} timeout={500}>
          {(state) => (
            <React.Fragment>
              <div
                style={{
                  opacity: state === "entered" ? 1 : 0,
                  transition: "all 0.5s",
                }}
                className={classes.Results}
              >
                {results ? listings : null}
              </div>
              <PageButtons
                dynPage={pagination}
                pageClick={(dir) => handlePage(dir)}
              />
            </React.Fragment>
          )}
        </Transition>
      ) : (
        <Spinner />
      )}
    </React.Fragment>
  );
};

export default React.memo(Results);
