import React, { useState } from "react";
import classes from "./Window.module.css";

// const invalidStyle = {
//   backgroundColor: "rgba(200,200,200,0.5)",
//   border: "1px solid #bdbdbd",
//   cursor: "not-allowed",
//   color: "black",
//   opacity: 0.6,
// };

const Window = (props) => {
  const [authMode, setAuthMode] = useState("signup");

  const authenticateHandler = (event) => {
    event.preventDefault();
    const inputs = document.querySelectorAll("input");
    if (authMode === "login") {
      props.clickHandler({
        email: inputs[0].value,
        pass: inputs[1].value,
        method: "login",
      });
    } else {
      props.clickHandler({
        name: inputs[0].value,
        email: inputs[1].value,
        pass: inputs[2].value,
        passConfirm: inputs[3].value,
        method: "signup",
      });
    }
  };

  const switchAuthHandler = () => {
    const isSignup = authMode === "signup";
    isSignup ? setAuthMode("login") : setAuthMode("signup");
  };

  return (
    <div className={classes.WindowContainer}>
      <div className={classes.Window}>
        <h1 className={classes.Title}>
          {authMode === "signup" ? "Sign up" : "Log in"}
        </h1>
        <form className={classes.Form} onSubmit={authenticateHandler}>
          {authMode === "signup" ? (
            <div className={classes.Field}>
              <label htmlFor="name">Your name</label>
              <input id="name" type="text" required />
            </div>
          ) : null}
          <div className={classes.Field}>
            <label htmlFor="email">Email Address</label>
            <input id="email" type="email" required />
          </div>
          <div className={classes.Field}>
            <label htmlFor="password">Password</label>
            <input id="password" type="password" required minLength="8" />
          </div>
          {authMode === "signup" ? (
            <div className={classes.Field}>
              <label htmlFor="passwordConfirm">Password Confirm</label>
              <input
                id="passwordConfirm"
                type="password"
                required
                minLength="8"
              />
            </div>
          ) : null}
          <button>{authMode === "signup" ? "Sign up" : "Log in"}</button>
        </form>
        <h2>
          {authMode === "signup"
            ? "Already have an account?"
            : "Don't have an account?"}{" "}
          <span className={classes.switchAuth} onClick={switchAuthHandler}>
            {authMode === "signup" ? "Log in" : "Sign up"}
          </span>
        </h2>
      </div>
    </div>
  );
};

export default Window;
