import React, { useEffect, useState } from "react";
import classes from "./View.module.css";
import axios from "axios";
import Spinner from "../../../components/UI/Spinner/Spinner";
import { BiLeftArrow, BiRightArrow } from "react-icons/bi";
import { GoLocation, GoStar } from "react-icons/go";
import { FaRegMoneyBillAlt } from "react-icons/fa";
import { TiTick } from "react-icons/ti";
import Calender from "react-calendar";
import Transition from "react-transition-group/Transition";
import { Carousel } from "react-responsive-carousel";
import styles from "react-responsive-carousel/lib/styles/carousel.min.css";
import "react-calendar/dist/Calendar.css";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import ConfirmModal from "./ConfirmModal/ConfirmModal";
import { loadStripe } from "@stripe/stripe-js";
import ErrorModal from "../../../components/Utility/errorModal/errorModal";
const stripePromise = loadStripe(
  "pk_test_51IVn2EJplsnbYwYSCOFxSKQOqmbCq14eHi2eY0onKhw81xQ4JQqsec51K5E0KEQEbzymD5lStwpk8RkuzAZiLG6k00uJtMyPip"
);

const View = (props) => {
  const [details, setDetails] = useState(null);
  const [images, setImages] = useState(null);
  const [showCalender, setShowCalender] = useState(false);
  const [dates, setDates] = useState([]);
  const [confirmModal, setConfirmModal] = useState(null);
  const [error, setError] = useState(false);

  useEffect(() => {
    axios.get(`/api/getaways/${props.mongoId}`).then((response) => {
      // mongoId is the slug
      if (response.data.data) {
        setDetails(response.data.data);
        setImages([
          response.data.data.coverImage,
          ...response.data.data.images,
        ]);
      }
    });
  }, [props.mongoId]);

  const showCalenderHandler = () => {
    setShowCalender(!showCalender);
    window.scrollTo({
      top: document.body.scrollHeight,
      behavior: "smooth",
    });
  };

  const calenderChangeHandler = (value) => {
    setDates(value);
  };

  const placeBooking = async (data) => {
    const checkoutData = {
      listingId: details._id,
      fromDate: data.datesBooked[0],
      toDate: data.datesBooked[1],
      totalPrice: data.totalPrice,
      days: data.days,
      slug: details.slug,
    };
    const response = await axios.post("/api/bookings/checkout", checkoutData);
    const stripe = await stripePromise;
    const result = stripe.redirectToCheckout({
      sessionId: response.data.sessionId,
    });
    if (result.error) {
      setError(true);
      setTimeout(() => {
        setError(false);
      }, 2000);
    }
  };

  const bookHandler = () => {
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
    setConfirmModal(true);
  };

  const hideModal = () => {
    if (confirmModal) {
      setConfirmModal(null);
    }
  };

  const redirectSignUp = () => {
    props.history.push("/signup");
  };

  const iconStyling = {
    verticalAlign: "text-top",
    width: "20px",
    height: "20px",
    padding: "5px",
    marginTop: "-4px",
  };

  const tickStyling = {
    verticalAlign: "text-bottom",
    color: "green",
  };

  const disabledStyling = {
    backgroundColor: "#bdbdbd",
    cursor: "not-allowed",
    opacity: 0.5,
    border: "none",
  };

  const months = [
    "January",
    "February",
    "March",
    "April",
    "May",
    "June",
    "July",
    "August",
    "September",
    "October",
    "November",
    "December",
  ];

  let view = <Spinner />;
  if (images) {
    view = (
      <React.Fragment>
        <div className={classes.CarouselContainer}>
          <Carousel
            showStatus={false}
            showThumbs={false}
            transitionTime={600}
            renderArrowPrev={(ClickHandler, hasPrev, label) =>
              hasPrev && (
                <BiLeftArrow
                  className={classes.CarouselLeft}
                  onClick={ClickHandler}
                />
              )
            }
            renderArrowNext={(ClickHandler, hasNext, label) =>
              hasNext && (
                <BiRightArrow
                  className={classes.CarouselRight}
                  onClick={ClickHandler}
                />
              )
            }
          >
            {images.map((img, i) => (
              <div key={img + i}>
                <img src={`/listings/${img}`} alt="databasePhoto" />
              </div>
            ))}
          </Carousel>
        </div>
        <div className={classes.DataView}>
          <h1>{details.name.replaceAll("-", " ")}</h1>
          <h3>
            <GoStar style={iconStyling} /> Average Rating:{" "}
            <strong>{details.ratingsAverage} / 5</strong>
          </h3>
          <h3>
            <FaRegMoneyBillAlt style={iconStyling} /> Price Per Night:{" "}
            <strong>£{details.pricePerNight}</strong>
          </h3>
          <h3>
            <GoLocation style={iconStyling} /> Location:{" "}
            <strong>{details.location}</strong>
          </h3>
          {Object.entries(details.detail).map(([detail, value]) => {
            return (
              <p key={detail + value}>
                {detail.includes("_") ? detail.replace("_", " ") : detail}:{" "}
                {typeof value === "boolean" ? (
                  <TiTick style={tickStyling} />
                ) : (
                  <strong>{value}</strong>
                )}
              </p>
            );
          })}
          <h2>Are you ready for your dream getaway ? Book now!</h2>
          {props.isAuth ? (
            <React.Fragment>
              <div className={classes.FromFields}>
                <span className={classes.labelFrom}>From</span>
                <input
                  placeholder="From"
                  className={classes.FromInput}
                  value={
                    dates.length === 0
                      ? "From"
                      : `${months[new Date(dates[0]).getMonth()]} ${new Date(
                          dates[0]
                        ).getDate()} ${new Date(dates[0]).getFullYear()}`
                  }
                  onChange={() => {}}
                  contentEditable={false}
                />
              </div>
              <div className={classes.ToFields}>
                <span className={classes.labelTo}>To</span>
                <input
                  placeholder="To"
                  className={classes.ToInput}
                  value={
                    dates.length === 0
                      ? "To"
                      : `${months[new Date(dates[1]).getMonth()]} ${new Date(
                          dates[1]
                        ).getDate()} ${new Date(dates[1]).getFullYear()}`
                  }
                  onChange={() => {}}
                  contentEditable={false}
                />
              </div>

              <button
                onClick={showCalender ? bookHandler : showCalenderHandler}
                disabled={dates.length === 0 && showCalender}
                style={
                  dates.length === 0 && showCalender ? disabledStyling : null
                }
              >
                {showCalender ? "Book now" : "Select Dates"}
              </button>
            </React.Fragment>
          ) : (
            <button onClick={redirectSignUp}>Sign up to book</button>
          )}
        </div>
      </React.Fragment>
    );
  }

  return (
    <React.Fragment>
      {details ? (
        <Transition in={images !== null} transitionTime={500} timeout={50}>
          {(state) => (
            <div
              style={{
                transform: state === "entered" ? "scale(1)" : "scale(0)",
                transition: "all 1s",
              }}
            >
              {confirmModal ? (
                <ConfirmModal
                  monthsRef={months}
                  selectedDates={dates}
                  bookingDetails={details}
                  confirmBooking={(data) => placeBooking(data)}
                  closeModal={hideModal}
                />
              ) : null}
              {error ? (
                <ErrorModal
                  restype="fail"
                  message="Unable to connect to checkout"
                />
              ) : null}
              {view}
            </div>
          )}
        </Transition>
      ) : (
        <Spinner />
      )}

      {showCalender ? (
        <Calender
          className={classes.Calender}
          selectRange={true}
          onChange={calenderChangeHandler}
        />
      ) : null}
    </React.Fragment>
  );
};

const mapStateToProps = (state) => {
  return {
    isAuth: state.isAuth,
  };
};

export default connect(mapStateToProps)(withRouter(View));
