import React from "react";

import classes from "./Reviews.module.css";
import userPhoto from "../../../assets/user.png";
import { AiFillStar, AiOutlineStar } from "react-icons/ai";

const rating = 5;
const stars = [1, 2, 3, 4, 5];

const reviews = (props) => {
  return (
    <section className={classes.Reviews}>
      <h1>What our customers say</h1>
      <div className={classes.Review}>
        <img src={userPhoto} alt="userPhoto" />
        <ul>
          {stars.map((star, i) => {
            return i < rating ? (
              <AiFillStar key={i * Math.random()} className={classes.icon} />
            ) : (
              <AiOutlineStar />
            );
          })}
        </ul>
        <p>
          Amazing services! I decided to book a viewing before renting, and it
          was amazing! Went ahead and booked it for 2 nights, best getaway I've
          had!{" "}
        </p>
      </div>
      <div className={classes.Review}>
        <img src={userPhoto} alt="userPhoto" />
        <ul>
          {stars.map((star, i) => {
            return i < rating ? (
              <AiFillStar key={i * Math.random()} className={classes.icon} />
            ) : (
              <AiOutlineStar />
            );
          })}
        </ul>
        <p>
          Amazing services! I decided to book a viewing before renting, and it
          was amazing! Went ahead and booked it for 2 nights, best getaway I've
          had!{" "}
        </p>
      </div>
      <div className={classes.Review}>
        <img src={userPhoto} alt="userPhoto" />
        <ul>
          {stars.map((star, i) => {
            return i < rating ? (
              <AiFillStar key={i * Math.random()} className={classes.icon} />
            ) : (
              <AiOutlineStar />
            );
          })}
        </ul>
        <p>
          Amazing services! I decided to book a viewing before renting, and it
          was amazing! Went ahead and booked it for 2 nights, best getaway I've
          had!{" "}
        </p>
      </div>
    </section>
  );
};

export default reviews;
